html, body {
  font-family: "Open Sans", sans-serif;
  color: #212529;
  background: #ffffff;
  font-weight: 400;
  font-size: 16px;
  font-feature-settings: "lnum";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  color: #212529;
  font-family: "Roboto", sans-serif;
  font-weight: bold;
  word-wrap: break-word;
  line-height: 1.2;
}
h1 span,
h2 span,
h3 span,
h4 span,
h5 span,
h6 span {
  display: inline-block;
}
h1 em,
h2 em,
h3 em,
h4 em,
h5 em,
h6 em {
  display: block;
  font-weight: 300;
  font-size: 1.125rem;
  font-style: italic;
}

strong {
  font-weight: 900;
}

p {
  font-size: 1rem;
  line-height: 1.7;
  font-family: "Open Sans", sans-serif;
}

h1 {
  font-size: 2.375rem;
}

h2 {
  font-size: 2.25rem;
}

h3 {
  font-size: 1.75rem;
}

h4 {
  font-size: 1.5rem;
}

h5 {
  font-size: 1.3125rem;
}

h6 {
  font-size: 1.125rem;
}

.color-red {
  color: #D56D5C;
}

.color-orange {
  color: #fab325;
}

.color-blue {
  color: #089DF5;
}

.color-green {
  color: #2CCB4D;
}

.link-danger {
  margin-top: 30px;
  display: block;
  text-decoration: none;
  color: #D56D5C;
}
.link-danger:hover, .link-danger:focus, .link-danger:active {
  color: #D56D5C;
}

.container {
  margin-left: auto;
  margin-right: auto;
}

.flex-container {
  display: flex;
  flex-wrap: wrap;
}

.flex {
  display: flex;
}

.text-left {
  text-align: left;
}

.text-center {
  text-align: center;
}

.text-right {
  text-align: right;
}

.align-center {
  align-items: center;
}

.justify-center {
  align-items: center;
}

.f-light {
  font-weight: 300;
}

.f-regular {
  font-weight: 400;
}

.f-medium {
  font-weight: 500;
}

.f-bold {
  font-weight: 600;
}

.f-black {
  font-weight: 900;
}

.bg-dark {
  background: #212529;
}

.p-r-0 {
  padding-right: 0 !important;
}

.sr-only {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

.page-head {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
}
@media (min-width: 1200px) {
  .page-head {
    top: 44px;
  }
}
.page-head nav {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
}
.page-head a.navbar-brand {
  padding-left: 35px;
}
@media (min-width: 900px) {
  .page-head a.navbar-brand {
    padding-left: 80px;
  }
}
@media (min-width: 1200px) {
  .page-head a.navbar-brand {
    padding-left: 0;
  }
  .page-head a.navbar-brand img {
    animation: menuAnim ease-in-out 0.5s forwards;
  }
}
.page-head img {
  max-width: 164px;
  height: auto;
  margin-top: 20px;
}
@media (min-width: 900px) {
  .page-head img {
    max-width: 218px;
  }
}
@media (min-width: 1800px) {
  .page-head img {
    max-width: 293px;
  }
}

.navbar-nav {
  position: relative;
  display: flex;
  padding: 0;
  width: auto;
  height: auto;
  background: transparent;
  padding-top: 25px;
  list-style: none;
}
.navbar-nav a {
  color: #fff;
  font-weight: bold;
  padding: 10px 20px;
  text-decoration: none;
  text-transform: uppercase;
  margin-bottom: 15px;
  font-size: 1.125rem;
  display: block;
  transition: color ease-in 0.25s;
}
.navbar-nav a:hover, .navbar-nav a:focus {
  color: #D56D5C;
}

* {
  margin: 0;
  box-sizing: border-box;
}

#main {
  background: #d7d7d7;
  overflow-x: hidden;
}

img {
  max-width: 100%;
}

img.hero__img {
  width: 100%;
}

.text-section {
  padding-top: 50px;
}
.text-section h2 {
  margin-bottom: 20px;
}

/* vendors */
.btn {
  padding: 15px 26px;
  line-height: 1.15;
  text-align: center;
  background: transparent;
  color: #212529;
  border-radius: 50px;
  letter-spacing: 2px;
  border: 1px solid #212529;
  text-transform: none;
  z-index: 1;
  overflow: hidden;
  font-family: "Roboto", sans-serif;
  font-weight: bold;
  transition: color 0.25s ease-in-out, background-color 0.25s ease-in-out;
}
.btn:hover, .btn:focus {
  text-decoration: none;
  cursor: pointer;
  color: #ffffff;
  background: #212529;
  outline: 0;
  border: 1px solid #212529;
}
.btn-secondary {
  background: #212529;
  color: #ffffff;
  border-radius: 50px;
  letter-spacing: 0.07em;
  border: 1px solid #212529;
}
.btn-secondary:hover, .btn-secondary:focus {
  color: #212529;
  background: transparent;
  outline: 0;
  border: 1px solid #212529;
}